
import fetch from 'isomorphic-unfetch'

const Index = (props) => (
  <div>
    <h1>Hello Next.js</h1>
    <pre><code>{props.shows}</code></pre>
    <a style={{color:'purple',backgroundColor:'orange', border: 'outset 1px darkgrey', padding:2, borderRadius:5}} 
       onClick={() => console.log('ja')}>ververs op de client</a>
  </div>
)

Index.fetchWhoami = async () => {
  const url = req ? 'http://whoami.docker.localhost/' : 'http://whoami.docker.localhost/'
  const res = await fetch(url)
  const data = await res.text()

  return data
}


Index.getInitialProps = async function({req}) {
  console.log(req)
  const data = await Index.fetchWhoami()

  console.log(data)
  return {
    shows: data
  }
}

export default Index
